.Projects{
    width: 100%;
    height: calc(100vh - 70px);
    /* height: 120vh; */
    /* background-color: green; */
    /* overflow-y: hidden; */
}
.Projects .NavPanel{
    width:200px;
    height: calc(100vh - 70px);
    /* height: 120vh; */
    background-color: aliceblue;
    border-right: 2px solid rgba(0,0,0,0.6);
    position: absolute;
    left: 0px;
    top: 70px;
    /* display: none; */
    z-index: 2;
    /* padding-top: 30px; */
    animation: navPanelAnims 0.5s 1 ease-out; 
}

.Projects .NavPanel .content{
    width: inherit;
    position: sticky;
    top: 80px;
    left: 0px;
}

.content div{
    width: 180px;
    height: 40px;
    background-color: red;
    color:black;
    font-size: 18px;
    border: 2px solid black;
    list-style: none;
    margin-bottom: 5px;
    margin-left: 10px;
    display: flex;
    /* text-align: center; */
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    animation: navPanelItemAnims 0.7s 1 ease-in;
}
.content div:hover{
    /* animation: itemHover 0.3s 1 ease-in-out; */
    transform: scale(1.1);
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 20px;
    transition: 0.25s;
}


.Projects .projectDescription{
    width:calc(100% - 200px);
    height: calc(100vh - 70px);
    /* height: 120vh; */
    position: absolute;
    left: 200px;
    top: 70px;
    background-image: url("../../assets/home_projects.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;

    /* display: flex; */
    /* justify-content: center; */
    /* align-items: center; */
    overflow-y: scroll;
}


@keyframes navPanelAnims {
    0% {height: 10%;}
    100% {height: 100%;}
}

@keyframes navPanelItemAnims {
    0% {opacity: 0; height: 0px; font-size: 0px;}
    100% {opacity: 1; height: 40px; font-size: 18px;}
}

@keyframes itemHover {
    0% {transform: scale(1);}
    100% {transform: scale(1.1);}
}
