body{
    display: block;
    margin: 0%;
    user-select: none;
}
@media only screen and (max-width: 600px){

    @media screen and (orientation: landscape) {
        html{
            transform: rotate(90deg);
            transform-origin: left top;
            width: 100vh;
            height: 100vw;
            margin: 0;
            position: absolute;
            top: 0%;
            left: 100%;
            
            overflow-x: hidden;
        }
    }
}