.Home {
    width: 100%;
    /* height: calc(100vh - 70px); */
    /* background-color: cornflowerblue; */
    /* background-image: url("../../assets/home_bg.jpg"); */
    /* background-size: 100% 100%; */
    /* background-repeat: no-repeat; */
}
::-webkit-scrollbar{
    display:none
}
@media only screen and (max-width: 600px) {
    .Home .pageOne{
        width: inherit;
        height: calc(100vh - 100px);
        /* display: flex; */
    }
    .Home .pageOne .content{
        width: inherit;
        height: 30vh;
        background-color:rgba(0, 0, 0, 0.6);
    }
    .Home .pageOne .image {
        width: inherit;
        height: calc(70vh - 100px);
        background-image: url("../../assets/home_bg.jpg");
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }
}

@media only screen and (min-width: 600px) {
    .Home .pageOne{
        width: inherit;
        height: calc(100vh - 100px);
        display: flex;
    }
    .Home .pageOne .content{
        width: 30vw;
        height: inherit;
        background-color:rgba(0, 0, 0, 0.6);
    }
    .Home .pageOne .image {
        width: 70vw;
        height: inherit;
        background-image: url("../../assets/home_bg.jpg");
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    .Home .page_about{
        width: inherit;
        height: 400px;
        background-color: lawngreen;
    }
    .Home .page_projects{
        width: inherit;
        height: 400px;
        background-color: gray;
    }
    .Home .page_contact{
        width: inherit;
        height: 400px;
        background-color: lawngreen;
    }
}
