.NavBar{
    width: 100%;
    height: 70px;
    position: sticky;
    left: 0;
    top: 0;
    background-color: black;
    color: blanchedalmond;
    z-index: 10;
}
.NavBar h1{
    padding: 0;
    margin: 0%;
    /* vertical-align:middle; */
}
.NavBarElements{
    display: flex;
    position: absolute;
    right: 20px;
    top: 15px;
}
.NavBarElements div{
    width: auto;
    height: 25px;
    margin-right: 15px;
    color: aliceblue;
}
.NavBarElements div:hover{
    transform: scale(1.1);
}
.NavBarElements .active{
    /* background-color: aliceblue; */
    color: #ffe555;
    margin-top: 5px;
}
.NavBarElements .inActive{
    color: aliceblue;
    margin-top: 5px;

}
@media only screen and (max-width: 600px) {
    
    .NavBarElements{
        width: 100%;
        display: flex;
        position: relative;
        right: 0px;
        top: 0px;
        /* transform: translateX(-50%); */
        /* border: 1px solid rebeccapurple; */

    }

    .NavBarElements div{
        /* width: 70px; */
        height: 20px;
        /* margin-right: 15px; */
        padding-left: 5px;
        padding-right: 5px;
        color: aliceblue;
        /* background-color: red; */
    }
}
@media only screen and (min-width: 600px) {}
