.Contact {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: cornflowerblue;
    animation: contactPageAnim 0.2s 1 ease-in-out;

}

.Contact .Content {
    width: 100%;
    height: 75%;
}

.Contact .Footer {
    width: 100%;
    height: 25%;
    position: sticky;
    left: 0;
    top: 100%;
    background-color: midnightblue;
    color: oldlace;
    justify-content: center;
    align-items: center;
    display: flex;
    border-radius: 10px 10px 0px 0px;
}
.Contact .Footer .title {
    width: 45%;
    font-size: 20px;
    margin-left: 5%;
}

.Contact .Footer span {
    margin-left: 10px;
}

@media only screen and (min-width: 600px) {
    .Contact .Footer .info {
        width: 50%;
        /* display: block; */
        /* text-align: left; */
    }
    .Contact .Content {
        display: flex;
    }
    .Contact .Content .contentBox{
        width:calc(100vw - 400px) ;
        height: 70%;
        margin-top: 5%;
        margin-left: 25px;
        margin-right: 25px;
        border: 2px solid black;


    }
    
    .Contact .Content .form{
        width:320px;
        height: 70%;
        border: 2px solid black;
        border-radius: 0px 15px 0px 15px;
        float: right;
        margin-right: 30px;
        margin-top: 5%;
        /* padding-top: 30px; */
        /* padding-bottom: 30px; */
    }
    .Contact .Content form{
        margin-top: 30px;
    }
    

    .Contact .Content .formElements{
        width: 280px;
        height: 30px;
        margin-bottom: 5px; 
        margin-left: 17px;
    }
    .Contact .Content .formText{
        height: 120px;
    }
    .Contact .Content .formSubmit{
        width: 100px;
        height: 30px;
        margin-left: 110px;
    }
}
@media only screen and (max-width: 600px) {
    .Contact .Footer {
        display: block;
    }

    .Contact .Footer .info {
        width: 80%;
        margin-left: 15%;
        margin-top: 10px;
    }
    .Contact .Content .contentBox{
        display: none;
    }

    .Contact .Content .form{
        width:320px;
        height: 60%;
        border: 2px solid black;
        border-radius: 0px 15px 0px 15px;
        position: relative;
        left: calc(50vw - 160px);
        top: 10%;
        
        /* margin-top: 10%; */
        /* padding-top: 30px; */
        /* padding-bottom: 30px; */
    }
    .Contact .Content form{
        margin-top: 25px;
    }

    .Contact .Content .formElements{
        width: 280px;
        height: 30px;
        margin-bottom: 5px; 
        margin-left: 17px;
    }
    .Contact .Content .formText{
        height: 120px;
    }
    .Contact .Content .formSubmit{
        width: 100px;
        height: 30px;
        margin-left: 110px;
    }
}


@keyframes contactPageAnim {
    0% { opacity: 0;}
    100% { opacity: 1;}
}